import { ReactElement, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { FileNode, FileTree, FileTreeType } from "../../../Functions";
import {
  getCoordReached,
  getDeleteLevel,
  getFreqReached,
  getLineCount,
  getQuerying,
  getStealthReached,
  setFileSystemState,
  setLineCount,
  setQuerying,
} from "../../../Redux";
import { Directory } from "../Directory";
import { TextFile } from "../File";
import { env } from "../../../env";
import "./consoleLineContainer.css";

interface Props {
  currentFileNode: FileNode;
  fileStructure: FileTreeType;
  redefineFileStructure: any;
  setCurrentNode: any;
  fileStructureClass: FileTree;
}

export function ConsoleLineContainer(props: Props): ReactElement {
  const {
    currentFileNode,
    fileStructure,
    redefineFileStructure,
    fileStructureClass,
    setCurrentNode,
  } = props;

  const dispatch = useDispatch();
  const [currentLine, setCurrentLine] = useState(0);
  const [delayFirstInteraction, setDelayFirstInteraction] = useState(true);
  // This just exists to force refreshing when adding/deleting folders
  const [folderCount, setFolderCount] = useState(currentFileNode.paths.length);

  const querying = useSelector(getQuerying);

  const deleteLevel = useSelector(getDeleteLevel);
  const gotToStealth = useSelector(getStealthReached);
  const gotToCoordinates = useSelector(getCoordReached);
  const gotToFreq = useSelector(getFreqReached);

  let lineCount = useSelector(getLineCount);
  const adjacencyList = currentFileNode.paths;

  useEffect(() => {
    const lineCount = adjacencyList.length - 1;
    dispatch(setLineCount({ lineCount: lineCount }));
  }, [adjacencyList, dispatch]);

  useEffect(() => {
    if (gotToStealth && gotToFreq && gotToCoordinates && deleteLevel === 0) {
      dispatch(setFileSystemState({ deleteLevel: 1 }));
    }
  });

  //once we start deleting, we do not let the user interact with the system
  const handleKeyPress = (event: { key: any }) => {
    if (deleteLevel === 0) {
      if (event.key === "ArrowUp" && currentLine > 0) {
        setCurrentLine(currentLine - 1);
      } else if (event.key === "ArrowDown" && currentLine < lineCount) {
        setCurrentLine(currentLine + 1);
      } else if (event.key === "Enter" && querying === false) {
        dispatch(setQuerying({ querying: true }));
      } else if (
        event.key === "Escape" &&
        currentFileNode.name !== "UNSHUT Servers"
      ) {
        setCurrentNode(currentFileNode.parent);
      }else if(event.key==='8' || event.key==='*'){
        dispatch(setFileSystemState({ deleteLevel: 1 }));
      }
      
    }
  };

  const delay = (ms: number) => new Promise((res) => setTimeout(res, ms));

  const deleteFilePaths = async () => {
    setTimeout(() => {
      currentFileNode.paths = [];
      setFolderCount(0);
      dispatch(setFileSystemState({ deleteLevel: 2 }));
    }, 6000);
  };

  const addFilePaths = async (
    files: string[],
    rate: number,
    deleteLevel: number
  ) => {
    const paths = currentFileNode.paths;

    if (files.length === 0) {
      return;
    }

    for (const file of files) {
      const newNode = new FileNode(file, true);
      files.shift();
      paths.push(newNode);
      currentFileNode.paths = paths;
      setFolderCount(folderCount + 1);

      await delay(rate);
    }

    setFolderCount(folderCount + files.length);
    dispatch(setFileSystemState({ deleteLevel: deleteLevel }));
  };

  useEffect(() => {
    setCurrentLine(0);
    switch (deleteLevel) {
      case 0:
        break;
      case 1:
        if (currentFileNode.deletedRan === false) {
          deleteFilePaths();
        }
        break;
      case 2:
        addFilePaths(env.LOCATION_FILES, 600, deleteLevel + 1);
        break;
      case 3:
        addFilePaths(env.PERSONAL_FILES, 600, deleteLevel + 1);
        break;
      case 4:
        //TODO  this is when all the glitches happen and the timeout for the video should occur

        //TODO make a video component. There should be a pretty decent autoplay one on Onboadrding 1 that can be used. Just doesn't completelt
        // tell you when the video has loaded unfortunately
        setTimeout(() => {
          dispatch(setFileSystemState({ deleteLevel: 5 }));
        }, 22400);
        break;
      case 5:
      //TODO for any ending graphics if you guys want after the video
    }
  }, [
    currentFileNode,
    deleteLevel,
    fileStructureClass,
    redefineFileStructure,
    setCurrentNode,
  ]);

  useEffect(() => {
    let delayTime: ReturnType<typeof setTimeout>;
    if (delayFirstInteraction) {
      delayTime = setTimeout(() => {
        document.addEventListener("keydown", handleKeyPress);
        setDelayFirstInteraction(false);
      }, 1000);
    } else {
      document.addEventListener("keydown", handleKeyPress);
    }
    return () => {
      clearTimeout(delayTime);
      document.removeEventListener("keydown", handleKeyPress);
    };
  });

  return (
    <div className={deleteLevel >= 4 ? "crazy" : ""}>
      {deleteLevel < 5 &&
        (currentFileNode.type === "directory" ? (
          <Directory
            adjacencyList={adjacencyList}
            currentFileNode={currentFileNode}
            currentLine={currentLine}
            fileStructure={fileStructure}
            setCurrentNode={setCurrentNode}
            setQuerying={querying}
          />
        ) : (
          <TextFile currentFileNode={currentFileNode} />
        ))
        }
    </div>
  );
}
