import { ReactElement, useEffect, useState } from "react";
import axios from "axios";
import { FileNode } from "../../../Functions/FileStructure/FileStructure";
import { useDispatch, useSelector } from "react-redux";
import { getCoordReached, getFreqReached, setFileSystemState } from "../../../Redux";
import {env} from '../../../env'
import './file.css'

interface Props {
  currentFileNode: FileNode;
}
  
  

   export const abbreviatePath = (filePath:string): string =>{
   return filePath
      .split("/")
      .slice(8)
      .map((val) => val.replace(/ /g, "+"))
      .join("/");
  }

export function TextFile(props: Props): ReactElement {
  const { currentFileNode } = props;
  const [fileContent, setFileContent] = useState("Loading");

  const gotToCoord = useSelector(getCoordReached)
  const gotToFreq = useSelector(getFreqReached)
  const dispatch = useDispatch();

  const filePath = abbreviatePath(currentFileNode.key);
  
  useEffect(()=>{
    if(filePath === env.COORDINATE_FILE && gotToCoord === false){
        dispatch(setFileSystemState({gotToCoordinates:true}))
    }
    if(filePath === env.FREQ_FILE && gotToFreq === false){
      dispatch(setFileSystemState({gotToFrequency:true}))
    }

  },[filePath])
  // const fetchFile = async (filePath: string) => {
  //   const baseURL =
  //     "https://unshut-visual-assets.s3.amazonaws.com/FileServerExperience/UNSHUT+Servers/";

  //   await axios.get(`${baseURL}${filePath}`).then((response: any) => {
  //     setFileContent(response.data);
  //   });
  // }

  

  try {
    // fetchFile(filePath);
  } catch (error) {
    console.log(error);
  }
  return (
    <div className="file">
      <p dangerouslySetInnerHTML={{ __html: fileContent }} />
    </div>
  );
}
