import { ReactElement } from "react";
import "./oldTimey.css";
import { useSelector } from "react-redux";
import { getDeleteLevel } from "../../../Redux";
type Props = {
  children?: React.ReactNode;
  onClick?: () => void;
};

export function OldTimeyTheme(props: Props): ReactElement {
  const deleteLevel = useSelector(getDeleteLevel);
  return (
    <div
      className={deleteLevel < 4 ? "oldTimey" : "oldTimeBlack"}
      onClick={props.onClick}>
      {props.children}
    </div>
  );
}
