import ReactPlayer from "react-player";
import './video.css';

interface Props {
  src: string;
}
export function Video({ src }: Props) {
  return (
    <div className="video">
      <ReactPlayer
        width={"100vw"}
        height={"100vh"}
        url={src}
        playing={true}
        muted={true}
        loop={false}
        playsinline={true}

      />
    </div>
  );
}
