import "./console.css";

import { ReactElement, useEffect, useRef, useState } from "react";

import { FileNode, FileTree, FileTreeType } from "../../../Functions";
import { ConsoleLineContainer } from "../../Molecules/ConsoleLinesContainer";
import { VideoContainer } from "../../Molecules/VideoContainer";
import { getDeleteLevel } from "../../../Redux";
import { useSelector } from "react-redux";

interface Props {
  currentFileNode: FileNode;
  fileStructure: FileTreeType;
  fileStructureClass: FileTree;
  redefineFileStructure: any;
  setCurrentNode: any;
}

export function Console(props: Props): ReactElement {
  const {
    currentFileNode,
    fileStructure,
    redefineFileStructure,
    setCurrentNode,
    fileStructureClass,
  } = props;

  const [containerStyle, setContainerStyle] = useState("contentContainer-file");

  const deleteLevel = useSelector(getDeleteLevel);

  useEffect(() => {
    if (currentFileNode.type === "directory") {
      setContainerStyle("contentContainer-directory");
    } else {
      setContainerStyle("contentContainer-file");
    }
  }, [currentFileNode]);

  const useAutoFocus = () => {
    const inputRef: React.RefObject<HTMLInputElement> = useRef(null);

    useEffect(() => {
      if (inputRef.current) {
        inputRef.current.focus();
      }
    }, []);

    return inputRef;
  };

  const scrollFocus = useAutoFocus();

  const handleClick = () => {
    if (scrollFocus.current) {
      scrollFocus.current.focus();
    }
  };

  return (
    <div
      className={deleteLevel < 4 ? "consoleBackground" : "consoleBlack"}
      onClick={handleClick}>
      {deleteLevel === 4 && (
        <VideoContainer url={`/embedded/hackableFiles/build/digitalWall.mp4`} />
      )}
      <div className="console">
        {deleteLevel !== 5 && (
          <h1 className="heading">
            {currentFileNode?.name === "UNSHUT Servers"
              ? ""
              : currentFileNode?.name}
          </h1>
        )}
        <div className={containerStyle} ref={scrollFocus} tabIndex={0}>
          <ConsoleLineContainer
            fileStructureClass={fileStructureClass}
            fileStructure={fileStructure}
            redefineFileStructure={redefineFileStructure}
            currentFileNode={currentFileNode}
            setCurrentNode={setCurrentNode}
          />
        </div>
      </div>
    </div>
  );
}
