import { configureStore } from "@reduxjs/toolkit";
import { rootReducer, RootState } from "./RootReducer";

export const Store = configureStore({
  reducer: rootReducer,
});

export type AppDispatch = typeof Store.dispatch;

export const getQuerying = (state: RootState) => state.fileSystem.querying;
export const getLineCount = (state: RootState) =>
  state.fileSystem.fileNodeLineCount;

export const getDeleteLevel = (state: RootState) =>
  state.fileSystem.deleteLevel;
export const getFileSystem = (state: RootState) => state.fileSystem;
export const getLogIn = (state: RootState) => state.fileSystem.loggedIn;
export const getStealthReached = (state: RootState) => state.fileSystem.gotToStealth;

export const getCoordReached = (state: RootState) => state.fileSystem.gotToCoordinates;
export const getFreqReached = (state: RootState) => state.fileSystem.gotToFrequency;
