import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface FileSystemState {
  fileNodeLineCount: number;
  querying: boolean;
  deleteLevel: number;
  loggedIn: boolean;
  gotToCoordinates: boolean;
  gotToStealth: boolean;
  gotToFrequency: boolean;
}

interface FileSystemPayload {
  fileNodeLineCount?: number;
  querying?: boolean;
  deleteLevel?: number;
  loggedIn?: boolean;
  gotToCoordinates?: boolean;
  gotToStealth?: boolean;
  gotToFrequency?: boolean;
}

interface SetQuerying {
  querying: boolean;
}

interface SetLineCount {
  lineCount: number;
}

interface SetDeleteLevel {
  deleteLevel: number;
}

interface SetLogIn {
  logIn: boolean;
}

//PAGES
const initialState: FileSystemState = {
  fileNodeLineCount: 0,
  querying: false,
  deleteLevel: 0,
  loggedIn: false,
  gotToCoordinates: false,
  gotToFrequency: false,
  gotToStealth: false,
};

const fileSystemSlice = createSlice({
  name: "pages",
  initialState,
  reducers: {
    setLineCount(state, { payload }: PayloadAction<SetLineCount>) {
      state.fileNodeLineCount = payload.lineCount;
    },
    setQuerying(state, { payload }: PayloadAction<SetQuerying>) {
      state.querying = payload.querying;
    },
    setDeleteLevel(state, { payload }: PayloadAction<SetDeleteLevel>) {
      state.deleteLevel = payload.deleteLevel;
    },

    setFileSystemState(state, { payload }: PayloadAction<FileSystemPayload>) {
      state.querying = payload.querying || state.querying;
      state.fileNodeLineCount =
        payload.fileNodeLineCount || state.fileNodeLineCount;
      state.deleteLevel = payload.deleteLevel || state.deleteLevel;
      state.loggedIn = payload.loggedIn || state.loggedIn;
      state.gotToCoordinates =
        payload.gotToCoordinates || state.gotToCoordinates;
      state.gotToStealth = payload.gotToStealth || state.gotToStealth;
      state.gotToFrequency = payload.gotToFrequency || state.gotToFrequency;
    },
    setLogIn(state, { payload }: PayloadAction<SetLogIn>) {
      state.loggedIn = payload.logIn;
    },
  },
});

export const fileSystem_slice = fileSystemSlice;

export const {
  setFileSystemState,
  setDeleteLevel,
  setQuerying,
  setLineCount,
  setLogIn,
} = fileSystem_slice.actions;
