import { directories } from "./Directories";
import { files } from "./Files";

const fileStructure = [...directories, ...files];

export class FileNode {
  isRoot: boolean;
  name: string;
  type: string;
  paths: any[];
  deleted: boolean;
  deleting: boolean;
  deletedRan: boolean;
  additionalClass: string;
  src: string;
  parent: FileNode | string;
  key: string;
  deleteable: boolean;
  constructor(
    name: string,
    deleteable: boolean,
    parent = "",
    key = "",
    src = "",
    isRoot = false
  ) {
    this.name = name;
    this.key = key ? key : name;
    this.deleting = false;
    this.deleted = false;
    this.type = src ? "file" : "directory";
    this.isRoot = isRoot;
    this.src = src;
    this.paths = [];
    this.parent = parent;
    this.additionalClass = "";
    this.deleteable = deleteable;
    this.deletedRan = false;
  }
  addPath(object: FileNode) {
    if (this.type === "file") {
      throw "cannot add file to a file";
    }
    this.paths = [...this.paths, object];
  }

  setToDelete() {
    this.deleted = true;
  }

  setParent(parent: FileNode) {
    this.parent = parent;
  }
  setToDeleting() {
    this.deleting = true;
  }
  setStyle(style: string) {
    this.additionalClass = style;
  }
}

export interface FileTreeType {
  [key: string]: FileNode;
}

export class FileTree {
  fileTree: FileTreeType;
  constructor(files: any) {
    this.fileTree = {};
    this.generateNodes(files);
    this.constructTree(files);
  }

  generateNodes(files: any) {
    for (const file of files) {
      this.fileTree[file.key] = new FileNode(
        file.name,
        file.deleteable,
        file?.parent,
        file?.key,
        file?.src
      );
    }
  }

  
  //Randomly deletes files from the directory (Old approach)
  deleteFileNodePaths(probability: number, currentNode: FileNode) {
    let list = currentNode.paths;
    if (list.length === 0) {
      currentNode.deleted = true;
      //@ts-ignore
      const parent = currentNode.parent;
      if (typeof parent !== "string") {
        const remove = parent.paths.findIndex(
          //@ts-ignore
          (path) => path.name === currentNode.name
        );

        parent.paths = parent.paths
          .slice(0, remove)
          .concat(parent.paths.slice(remove + 1));
        currentNode.deletedRan = true;
      }
    }
    if (currentNode.deletedRan === false) {
      for (let i = 0; i < list.length; i++) {
        const pathNode = this.fileTree[list[i].key];

        if (pathNode.deleteable === true) {
          const rand = Math.floor(Math.random() * 111);
          if (probability <= rand) {
            currentNode.paths = currentNode.paths
              .slice(0, i)
              .concat(currentNode.paths.slice(i + 1));
            currentNode.deletedRan = true;
          }
        }
      }
    }
    return currentNode;
  }

  constructTree(files: any[]) {
    for (const file of files) {
      const currentNode = this.fileTree[file.key];
      if (currentNode.parent) {
        const parentNode = this.fileTree[file.parent];
        currentNode.parent = parentNode;
      }
      if (file?.paths?.length > 0) {
        for (const destination of file.paths) {
          const destinationNode = this.fileTree[destination];
          if (!destinationNode) {
          }
          currentNode.paths.push(destinationNode);
        }
      }
    }
  }
}

export const generateFullFileStructure = () => {
  return new FileTree(fileStructure);
};
