import { ReactElement, useEffect } from "react";
import { FileNode } from "../../../Functions";
import { ConsoleLine } from "../../Atoms";
import "./directory.css";
import { abbreviatePath } from "../File";
import { env } from "../../../env";
import { useDispatch, useSelector } from "react-redux";
import {
  getCoordReached,
  getFreqReached,
  getStealthReached,
  setFileSystemState,
} from "../../../Redux";

interface Props {
  adjacencyList: FileNode[];
  currentFileNode: FileNode;
  currentLine: number;
  setCurrentNode: any;
  setQuerying: any;
  fileStructure: any;
}

export function Directory(props: Props): ReactElement {
  const {
    adjacencyList,
    currentFileNode,
    currentLine,
    setCurrentNode,
    setQuerying,
    fileStructure,
  } = props;

  const directoryPath = abbreviatePath(currentFileNode.key);

  const gotToStealth = useSelector(getStealthReached);
  const gotToCoord = useSelector(getCoordReached);
  const gotToFreq = useSelector(getFreqReached);
  const dispatch = useDispatch();

  useEffect(() => {
    if (
      directoryPath === env.STEALTH_FOLDER &&
      gotToStealth === false &&
      gotToCoord === true &&
      gotToFreq === true
    ) {
      dispatch(setFileSystemState({ gotToStealth: true }));
    }
  }, [directoryPath, dispatch, gotToCoord, gotToFreq, gotToStealth]);

  return (
    <div className="directory">
      {adjacencyList.map((fileNode: FileNode, i: number) => {
        return currentLine === i ? (
          <ConsoleLine
            currentFileNode={currentFileNode}
            fileNode={fileNode}
            fileStructure={fileStructure}
            key={i}
            active={true}
            setQuerying={setQuerying}
            setCurrentNode={setCurrentNode}
          />
        ) : (
          <ConsoleLine fileNode={fileNode} key={i} active={false} />
        );
      })}
    </div>
  );
}
