import "./consoleLine.css";

import React, { ReactElement, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { FileNode, FileTreeType } from "../../../Functions";
import { getQuerying, setQuerying } from "../../../Redux";

interface Props {
  fileNode: FileNode;
  active?: boolean;
  setQuerying?: any;
  fileStructure?: FileTreeType;
  setCurrentNode?: any;
  currentFileNode?: FileNode;
}

export function ConsoleLine(props: Props): ReactElement {
  const { fileNode, active = false, setCurrentNode, currentFileNode } = props;

  const querying = useSelector(getQuerying);
  const dispatch = useDispatch();

  useEffect(() => {
    if (querying && active === true && currentFileNode) {
      const foundNode = currentFileNode.paths.find((node) => {
        return node.name === fileNode.name;
      });
      if (foundNode) {
        setCurrentNode(foundNode);
      }
      dispatch(setQuerying({ querying: false }));
    }
  }, [
    active,
    currentFileNode,
    dispatch,
    fileNode.name,
    querying,
    setCurrentNode,
  ]);

  const buildDir = '/embedded/hackableFiles/build'

  return (
    <div
      className={`consoleLine ${active && "active"} ${
        fileNode.additionalClass
      }`}>
      <div className="consoleLineImage">
        {fileNode.type === "directory" ? (
          <img
            alt="directoryIcon"
            className="directoryIcon"
            src={active ? `${buildDir}/images/fileBlack.png` : `${buildDir}/images/fileWhite.png`}
          />
        ) : (
          <img
            alt="fileIcon"
            className="directoryIcon"
            src={active ? `${buildDir}/images/docBlack.png` : `${buildDir}/images/docWhite.png`}
          />
        )}
      </div>
      <div className="consoleLineText">
        <p>{fileNode.name}</p>
      </div>
    </div>
  );
}
